@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
        'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.block_input{position:relative}.label_with_radio{position:absolute;top:-13px;left:55px;display:block;padding:15px 0;color:#368686;font-size:12px}.label_with_out_radio{position:absolute;top:-10px;left:10px;display:block;padding:15px 0;color:#368686;font-size:12px}input{display:block;width:300px;border-radius:5px;border:1px solid #565656}.input_with_out_label{max-width:100px;padding:9px}.input_with_label{padding:20px 15px 5px;margin:20px 0}input:hover{border:1px solid aqua}input:focus{border:1px solid blue}input[type='submit'],input[type='button']{width:150px;padding:10px;margin:20px 0}input[type='radio']{display:inline-block;width:15px;height:15px;margin:0 35px}input[type='number']::-webkit-outer-spin-button,input[type='number']::-webkit-inner-spin-button{-webkit-appearance:none}input[type='checkbox']{display:none}.checkbox-label+label{position:relative;-webkit-transform:translateY(25%);transform:translateY(25%);display:inline-block;width:20px;height:20px;border-radius:20px;border:3px solid orange;margin-right:10px}.checkbox-label:checked+label{border-color:tomato;background:radial-gradient(circle at left 46% top 48%, tomato 40%, white 45%)}.checkbox-label:hover+label{border:3px solid red}textarea{width:95%;height:200px}

.login_container{text-align:center;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);padding:20px;width:340px}.login-reg{padding:15px;border:none;background-color:lightblue;margin:0 20px 0 0}

.button_back{border:none;background:none;font-size:25px;font-weight:bold;display:block}.button_back::before{content:'< '}.item-name{font-weight:500;font-size:18px}.button_edit{display:inline-block;position:absolute;right:50px;bottom:60px;width:50%;height:50px;background-color:green;color:white;border:none;border-radius:5px}.button_add{display:inline-block;position:absolute;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);bottom:60px;width:70%;height:50px;background-color:green;color:white;border:none;border-radius:5px}.button_done{display:inline-block;position:absolute;left:70px;background:none;bottom:60px;width:50px;height:50px;border:none;border-radius:5px;font-size:35px}.button_delete{display:inline-block;position:absolute;left:150px;background:none;bottom:60px;width:50px;height:50px;border:none;border-radius:5px;font-size:35px}

.container{width:90%;margin:50px auto}#add_task{position:absolute;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);bottom:60px;width:70%;height:50px;background-color:tomato;color:white;border:none;border-radius:5px}.task_item{font-size:18px;font-weight:500}.item_name{font-size:20px;font-weight:500}.task_li{margin:1px 0;padding:10px;border-bottom:1px solid lightgray}.container_tasks{overflow:auto;max-height:300px}

.day{flex:none;flex-basis:80px;height:100px;margin:0 20px;border:1px solid lightgray;border-radius:15px}.today{flex:none;flex-basis:80px;height:100px;margin:0 20px;border:1px solid lightgray;border-radius:15px;background-color:black;color:white;font-weight:bold}.day>div,.today>div{width:100%;margin:5px 0 0 0;text-align:center;z-index:5}.month_yaer{font-size:12px}.day_name{font-size:20px;height:20%}.date{font-size:25px;font-weight:bold;height:30%}

.calendar{display:flex;overflow:scroll}

.task_page-container{margin:50px}.button_sign-out{position:absolute;right:50px;top:55px;padding:10px;border:none;background:none;color:tomato;font-size:20px}

*{font-family:'Montserrat', sans-serif;color:#565656}

